 /* Base Styles */
body {
    font-family: 'Arial', sans-serif;
    background-color: #000036; /* Dark space background */
    color: #e0e0e0;
    margin: 0;
    padding: 0;
}

/* Container */
.container {
    width: 80%;
    margin: auto;
    padding: 20px;
}

/* Header */
.header {
    text-align: center;
    padding: 20px 0;
    /* border-bottom: 2px solid #ffffff; /* Green border */ 
}

.header h1 {
    color: #ffffff; /* Green heading */
    font-size: 3em;
    margin-bottom: 10px;
}

.header p {
    font-style: italic;
    font-size: 1.5em;
    color: #b5b5b5;
}

/* Content */
.content {
    margin-top: 30px;
}

.profile-section, .cv-section {
    margin-bottom: 30px;
    background-color: #5c4be1;
    padding: 20px;
    border-radius: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
}

.profile-section h2, .cv-section h2 {
    color: #ffffff; /* Green subheadings */
    font-size: 2em;
    border-bottom: 2px solid #ffffff;
    padding-bottom: 10px;
    margin-bottom: 20px;
}

.profile-section p, .cv-section p {
    font-size: 1.1em;
    line-height: 1.8em;
    color: #d3d3d3;
}

/* Footer */
.footer {
    text-align: center;
    padding: 20px 0;
    margin-top: 50px;
    border-top: 2px solid #ffffff;
    color: #b5b5b5;
}

.footer p {
    margin: 0;
}

.current {
    color: green;
 }